import Vue from 'vue'
import Router from 'vue-router'
import Corpus from '@/components/Corpus'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Corpus',
      component: Corpus
    },
  ],
  mode: 'history'
})



