<template>
  <div id="corpus">
    <div class="container">
      <div class="web-title">
        <h1>文章分词统计工具</h1>
        <!--        <h3>已累计服务{{use_count}}次</h3>-->
      </div>
      <b-alert variant="danger" show v-if="message">{{message}}</b-alert>
      <div class="text-inputs" style="margin-top: 5%">
        <div class="row">
          <div class="col-md-8">
            <b-form-textarea
              id="textarea-state"
              v-model="content"
              :state="content.length < 10000"
              placeholder="输入文章内容，不超过1万个字"
              rows="19"
              required
              style="padding-inline: inherit;resize: none"
            ></b-form-textarea>
          </div>
          <div class="col-md-4">
            <div class="row">
              <div class="col-md-6">
                <b-form-checkbox
                  v-model="is_delete_punctuation"
                  name="checked"
                  switch> 删除常用标点
                </b-form-checkbox>
                <br>
              </div>
              <div class="col-md-6">
                <b-button @click="compute_content" variant="success">提交</b-button>
              </div>
            </div>
            <div class="row">
              <div class="col-md-6">
                分词字典
                <b-form-textarea
                  id="textarea-state"
                  v-model="user_words"
                  :state="user_words.length < 500"
                  rows="16"
                  placeholder="每行一个词"
                  style="box-shadow: 2px 2px 3px inset;resize: none"
                ></b-form-textarea>
              </div>
              <div class="col-md-6">

                停用词字典
                <b-form-textarea
                  id="textarea-state"
                  v-model="stop_words"
                  :state="stop_words.length < 500"
                  rows="16"
                  placeholder="每行一个词"
                  style="box-shadow: 2px 2px 3px inset;resize: none"
                ></b-form-textarea>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="res-table" v-if="showPic">
        <div class="row">
          <div class="col-md-6" style="height: 300px">
            <ve-bar :data="wordCount" :settings="wordCountSettings"></ve-bar>
          </div>
          <div class="col-md-6">
            <ve-bar :data="segCount" :settings="segCountSettings"></ve-bar>
          </div>
        </div>
        <div class="row">
          <!--          <div class="col-md-6">-->
          <!--            <ve-wordcloud :data="wordCloud" :settings="wordCloudSettings"></ve-wordcloud>-->

          <!--          </div>-->
          <div class="col-md-6">
            <ve-bar :data="punctuationCount" :settings="punctuationCountSettings"></ve-bar>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <ve-wordcloud :data="wordCloud" :settings="wordCloudSettings"></ve-wordcloud>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    data() {
      this.wordCountSettings = {
        metrics: ['词频分布'],
        dataOrder: {
          label: '词频分布',
          order: 'desc'
        }
      }
      this.segCountSettings = {
        metrics: ['词性分布'],
        dataOrder: {
          label: '词性分布',
          order: 'desc'
        }
      }
      this.wordCloudSettings = {
        shape: 'diamond'
      }
      this.punctuationCountSettings = {
        metrics: ['标点分布'],
        dataOrder: {
          label: '标点分布',
          order: 'desc'
        }
      }
      return {
        use_count: 379,
        content: "",
        user_words: "",
        message: "",
        stop_words: "的\n地\n得\n你\n我\n他\n了\n在\n是\n",
        is_delete_punctuation: false,
        wordCount: {},
        segCount: {},
        punctuationCount: {},
        wordCloud: {},
        showPic: false
      }
    },
    methods: {
      compute_content() {
        if (this.content.length < 5) {
          this.message = "内容过少"
        } else {
          const url = "http://127.0.0.1:5000/corpus"
          axios.post(url, {
            content: this.content,
            user_words: this.user_words,
            stop_words: this.stop_words,
            delete_punctuation: this.is_delete_punctuation
          }).then(res => {
            this.showPic = true
            this.message = ''
            this.wordCount = {
              columns: ["词名称", "词频分布"],
              rows: res.data.word_count
            }
            this.punctuationCount = {
              columns: ['标点名称', '标点分布'],
              rows: res.data.punctuation_count
            }
            this.segCount = {
              columns: ['词性', '词性分布'],
              rows: res.data.seg_count
            }
            this.wordCloud = {
              columns: ["词名称", "词频分布"],
              rows: res.data.word_cloud
            }
          }).catch(error => {
            console.log(error)
          })
        }

      }
    }
  }
</script>

<style>
  .web-title {
    margin-top: 3%;
    text-align: center;
    font-size: xx-large;
    font-family: 'Consolas', 'Deja Vu Sans Mono', 'Bitstream Vera Sans Mono', monospace;
  }

  .res-table {
    margin-top: 40px;
  }
</style>



